}
header .navigation li:hover{
    background: rgba(0, 139, 139, 0.74);
    cursor: pointer;
    border-radius: 6px;
}
header .navigation li a {
    color:rgb(255, 255, 255);
    text-decoration: none;
    font-weight: 400;
    border-radius: 3px;
}
.obsv-image{
    height: 300px;
    width: 600px;
}
@media screen and (max-width: 1000px) {
    nav{
        margin: 24px 20px;
    }
    header {
        height: 10vh;
        width: 100%;
        background: rgb(0, 52, 65);
    }
    header .logo {
        height: 35px;
        width: 35px;
        border-radius: 50%;
        margin-left: 1%;
        margin-right: 1%;
    }
    header div{
        display: flex;
        flex-direction: row;
        /* width: 100%; */
        justify-content: center;
        /* gap: 1rem; */
    }
    header div h1 {
        font-size: 1rem;
        color: white;
        /* width: 100%; */
        margin-top: 10px;
    }
    header .navigation{
        display: flex;
        flex-direction: row;
        gap: 1rem;
    }
    header{
        display: flex;
        flex-direction: column;
    }
    header .navigation li {
        font-size: 12px;
        list-style: none;
        margin-left: 20px;
        padding: 8px 1px;
    }
    header .navigation li:hover{
        background: rgba(0, 139, 139, 0.74);
        cursor: pointer;
        border-radius: 6px;
    }
    header .navigation li a {
        color:rgb(255, 255, 255);
        text-decoration: none;
        font-weight: 400;
        border-radius: 3px;
    }
    .obsv-image{
        height: 300px;
        width: 600px;
    }
    .language-cpp{
        height: auto;
        width: 80vw;
    }
    .mth{
       font-size: 1rem;
       width: 40%;
    }
    .instruc, .detl, .detl-1,.obs {
        text-align: center;
        font-size: 0.8rem;
        /* margin: 1.5%; */
    }
    .detl {
        font-size: 0.8rem;
    }
    .methods{
        font-size: 0.6rem;
    }
}
@media screen and (max-width: 480px) {
    header div{
        width: 45%;
    }
    header div h1{
        font-size: 0.8rem;
    }
    header .navigation li{
        font-size: 0.6rem;
    }
    .mth{
        font-size: 0.6rem;
        width: 60%;
    }
    .desc-heading{
        width: 100%;
    }
    .instruc, .detl, .detl-1,.obs {
        text-align: center;
        font-size: 0.8rem;
    }
    .detl {
        font-size: 0.8rem;
    }
    .nxt{
        margin: 2% auto;
        justify-content: center;
        align-items: center;
        display: flex;
        padding: 0.5% 1%;
        font-size: 1.5rem;
        border: 2px 2px 0px 0px solid black;
        cursor: pointer;
        background-color: #2196f3;
        color: white;
    }
    .cont {
        justify-content: center;
        align-items: center;
    }
    
    .table-anim {
        /* border: 10px solid #2196f3; */
        box-shadow: 0 0 0 0px black,0 0 0 10px #2196f3;
        z-index: -1;
        margin: 2% auto;
        font-size: 0.4rem;
        text-align: center;
        width: 50%;
        border-collapse: collapse;
    }
    .methods{
        font-size: 0.2rem;
    }
}
nav {
    font-size: 1rem;
    border-radius: 10px;
    height: auto;
    background: rgb(0, 30, 37);
    margin: 20px 20px;
    display: flex;
    flex-flow: wrap row;
    justify-content: space-around;
    font-weight: bold;
    color: honeydew;
    z-index: 1;
    box-shadow: 0 4px 8px 0 rgb(0 0 0 / 20%), 0 6px 20px 0 rgb(0 0 0 / 19%);
}
nav .boxq {
    transition: 0.4s ease;
    height: 100px;
    width: 115px;
    text-align: center;
    margin: 1rem;
    border: 2px solid rgb(236, 236, 236);
    border-radius: 5px;
    display: flex;
    align-items: center;
}
nav .boxq:hover {
    background: rgba(0, 255, 255, 0.589);
    color: white;
}
nav .boxq a {
    display: block;
    margin-left: auto;
    margin-right: auto;
    text-decoration: none;
    color: white;
}
nav .boxq a img {
    margin-bottom: 2px;
}
.exp-heading {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1rem;
    margin-top: 90px;
    text-align: center;
    margin-bottom: 30px;
    font-weight: bold;
}

.icon {
    display: inline-block;
    border-radius: 50%;
    height: 45px;
    width: 45px;
}

.container > .box {
    border: 1.5px rgba(0, 12, 12, 0.383) solid;
    border-radius: 0.5rem;
    font-size: 1rem;
    padding: 5px 20px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    margin: 5%;
    box-shadow: 0 4px 8px 0 rgb(0 0 0 / 20%), 0 6px 20px 0 rgb(0 0 0 / 19%);
}

.exp .container .box .title{
    line-height: 1.8;
    margin-top: 5%;
    margin-left: 5%;
    font-weight: bold;
}

.container .box .desc {
    line-height: 1.8;
    text-decoration: none;
    justify-content: center;
    display: flex;
    font-weight: bold;
    flex-direction: column;
    margin-top: 2%;
    margin-left: 4%;
    margin-bottom: 2%;
}
@media screen and (max-width:800px) {
    .exp-heading{
        font-size: 0.8rem;
        text-align: center;
    }
    img{
        width: 200px;
    }
    .obsv-image{
        height: 170px;
        width: 250px;
    }
    .language-cpp{
        height: auto;
        width: 70vw;
    }
    .container > .box {
        font-size: 0.7rem;
    }
    .instruc, .detl, .detl-1,.obs {
        text-align: center;
        font-size: 0.8rem;
    }
    .methods{
        font-size: 0.6rem;
    }
}

.cont {
    display: flex;
}

.det {
    display: none;
}

.tblDt {
    opacity: 0;
}
.mth{
    background-color: rgb(0, 62, 62);
    width: 30%;
    color: white;
    padding: 5px 10px;
    border-radius: 0px 20px 0px 20px;
    margin-top: 20px;
}
.instruc, .detl, .detl-1,.obs {
    text-align: center;
    margin: 1.5%;
}
.det{
    text-align: center;
    font-size: 1.2rem;
    margin-top: -1%;
}
.detl {
    font-size: 1.2rem;
}
.nxt{
    margin: 2% auto;
    justify-content: center;
    align-items: center;
    display: flex;
    padding: 0.5% 1%;
    font-size: 1.5rem;
    border: 2px 2px 0px 0px solid black;
    cursor: pointer;
    background-color: #2196f3;
    color: white;
}
.cont {
    justify-content: center;
    align-items: center;
}

.table-anim {
    /* border: 10px solid #2196f3; */
    box-shadow: 0 0 0 0px black,0 0 0 2px #2196f3;
    z-index: -1;
    margin: 2% auto;
    font-size: 1rem;
    text-align: center;
    width: 50%;
    border-collapse: collapse;
}

.table-anim tr>td {
    padding: 0.5% 0.1%;
    border: 2px solid black;
    z-index: 100;
}
.code-2,.code-3,.code-4,.code-5,.code-6{
    display: none;
}

iframe{
    width: 100%;
    height: 700px;
}
.container,iframe> html{
    overflow-y: hidden;
}
